export const FloorplanPanelWrapper = styled.div<I_DisplayPanelFloorplan>`
  position: absolute;
  width: 30%;
  height: 100%;
  transition: var(--transition-smooth);
  display: flex;
  align-items: center;
  background: ${props => props.bgColor};
  top: 0;
  pointer-events: auto;
  transform: none;
  z-index: 10;
  &.wall {
    z-index: 2;
  }
  &.hide {
    pointer-events: none;
    z-index: -1;
    ${props =>
      props.position === 'right'
        ? css`
            transform: translateX(100%);
          `
        : css`
            transform: translateX(-100%);
          `}
  }
  &.basekrpano {
  }
  &.default {
    ${props =>
      props.position === 'right'
        ? css`
            right: 0;
            top: 0;
            height: 100%;
          `
        : css`
            left: 0;
            top: 94px;
            height: calc(100% - 94px);
          `}
  }
  &.custom1 {
    height: calc(100% - 50px);
  }
  ${props =>
    props.position === 'right'
      ? css`
          right: 0;
        `
      : css`
          left: 0;
        `}
  .main {
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
  .react-transform-wrapper {
    overflow: visible;
    z-index: 2;
  }
`;

export const FloorplanBoxWrapper = styled.div<I_DisplayBoxFloorplan>`
  position: absolute;
  transition: var(--transition-smooth);
  background: radial-gradient(50% 50% at 50% 50%, #626262 0%, #000000 100%);
  background: ${props => props.bgColor};
  border-radius: 12px;
  overflow: hidden;
  border: 2px solid #fff;
  transform: none;
  z-index: 10;
  &.hide {
    opacity: 0;
    z-index: -1;
    pointer-events: none;
    ${props =>
      props.position === 'bottomLeft'
        ? css`
            transform: translateX(-4rem);
          `
        : props.position === 'topLeft'
        ? css`
            transform: translateX(-4rem);
          `
        : props.position === 'topRight'
        ? css`
            transform: translateX(4rem);
          `
        : css`
            transform: translateX(4rem);
          `}
  }
  &.basekrpano {
    /* opacity: 0.5; */
  }
  &.default {
  }
  &.custom1 {
  }
  ${props =>
    props.position === 'bottomLeft'
      ? css`
          bottom: ${props.offsetBottom}px;
          left: ${props.offsetLeft}px;
          right: unset;
          top: unset;
        `
      : props.position === 'topLeft'
      ? css`
          top: ${props.offsetTop}px;
          left: ${props.offsetLeft}px;
          bottom: unset;
          right: unset;
        `
      : props.position === 'topRight'
      ? css`
          top: ${props.offsetTop}px;
          right: ${props.offsetRight}px;
          bottom: unset;
          left: unset;
        `
      : css`
          bottom: ${props.offsetBottom}px;
          right: ${props.offsetRight}px;
          left: unset;
          top: unset;
        `}
`;

